<template>
	<div class="tags-container">
		<!-- <el-dialog :visible.sync="tagVisible" title="提示"> -->
		<el-dialog :visible.sync="localDialogVisible" :title="dialogTitle">

			<el-tag v-for=" item in items" :key="item.label" :type="item.type" class="tag"
				@click="clickEvet(item.code,item.type)">
				{{ item.label }}
			</el-tag>
		</el-dialog>
	</div>
</template>


<style>
	.tag-container {
		display: flex;
		flex-wrap: wrap;
		gap: 10px;
		justify-content: flex-start;
	}

	.el-tag {
		cursor: pointer;
		margin-bottom: 30px;
		/* 确保垂直间距 */
		margin-left: 10px;
		font-size: 15px;
	}

	/* 响应式布局 */
	@media (max-width: 768px) {
		.tag-container {
			flex-direction: column;
			align-items: flex-start;
		}
	}

	.el-dialog {
		line-height: 1.0;
		margin-top: 30px;
		text-align: left;

	}

	.el-dialog__title {
		font-size: 15px;
		/* 调整标题字体大小 */
	}

	.el-button {
		/* 无框 透明 */
		border: none;
		background-color: transparent;
	}
</style>

<script>
	import axios from 'axios';
	import moment from 'moment-timezone';
	export default {
		props: {
			dialogVisible: {
				type: Boolean,
				required: true
			},
			dialogTitle: {
				type: String,
				required: true
			},
			sumList: {
				type: Array,
				required: true,
				default: () => []
			},
			sumType: {
				type: String,
				required: true
			}
		},
		watch: {
			dialogVisible(newVal) {
				this.localDialogVisible = newVal;

			},
			localDialogVisible(newVal) {
				if (newVal) {
					console.log("拿数据")
					if (this.localDialogVisible) {
						this.getData()
					}
				} else {
					console.log("清洗数据")
					this.items = []
				}
				this.$emit('update-dialog-visible', newVal);
			}
		},
		methods: {
			getData() {

				if (this.sumType == 'label') {

					if (this.sumList != null) {
						this.sumList.forEach(v => {
							this.items.push({
								type: 'label',
								label: v['labelName'] + "(" + v['labelNum'] + ")",
								code: v['labelCode']
							})
						})
					}
				} else {

					if (this.sumList != null) {
						this.sumList.forEach(v => {
							this.items.push({
								type: 'category',
								label: v['categoryName'] + "(" + v['categoryNum'] + ")",
								code: v['categoryCode']
							})
						})
					}
				}

			},

			clickEvet(code, type) {
				let params = {
					code: code,
					type: type
				}
				axios.get("http://47.120.66.18:8899/api/articles/list/article-by-type-code", {
					// axios.get("http://127.0.0.1:8899/api/articles/list/article-by-type-code", {
					params
				}).then(response => {
					this.articles = response.data;
					for (let num in this.articles) {
						let date = moment(this.articles[num].createTime).tz('UTC'); // 设置时区为 UTC
						this.articles[num].createTime = moment(date).format('YYYY-MM-DD')
						if (null != this.articles[num].labelName) {
							let stringAsArray1 = this.articles[num].labelName.split(",");
							this.articles[num].labelNameList = stringAsArray1;
						}
						if (null != this.articles[num].categoryName) {
							let stringAsArray2 = this.articles[num].categoryName.split(",");
							this.articles[num].categoryNameList = stringAsArray2;
						}
					}

				}).then(a => {
					this.$bus.$emit('update-articles-data', this.articles);
				});

			}
		},
		data() {
			return {
				localDialogVisible: this.dialogVisible,
				items: [],
				articles: [],

			}
		}
	}
</script>