<template>
	<div class="profile-card">
		<p><el-image style="width: 150px; height: 150px" :src="avatarUrl" fit="fill"></el-image></p>
		<p style="font-size: 18px; ">
			{{ username }}
		</p>
		<p class="description">{{ description }}</p>
		<div class="stats">
			<nav>
				<el-button><span class="stat-item">文章 |{{articleNum}}|</span></el-button>
				<el-button @click="openDialog('category')"><span class="stat-item">分类
						|{{articleLabelNum}}|</span></el-button>
				<el-button @click="openDialog('label')"><span class="stat-item">标签
						|{{articleCategoryNum}}|</span></el-button>
			</nav>
		</div>

		<CategoryLabelTag :dialog-visible.sync="showDialog" @update-dialog-visible="handleCloseDialog"
			:dialogTitle="dialogTitle" :sumList="sumList" :sumType="sumType" />


	</div>
</template>

<script>
	import axios from 'axios';
	// import {
	// 	ref
	// } from 'vue';
	import CategoryLabelTag from './category-label-tag.vue';

	export default {
		data() {
			return {
				avatarUrl: require('@/assets/logo.png'), // 替换为实际的头像 URL
				username: 'MG',
				description: '不聪明',
				articleNum: '',
				articleLabelNum: '',
				articleCategoryNum: '',
				showDialog: false,
				dialogTitle: "",
				labelSumList: [],
				categorySumList: [],
				sumList: [],
				sumType: ''

			};
		},
		components: {
			CategoryLabelTag
		},
		created() {

			axios.get("http://47.120.66.18:8899/api/articles/list/article-summary").then(response => {
				let articleSummary = response.data;
				this.articleNum = articleSummary['articleNum']
				this.articleLabelNum = articleSummary['articleLabelNum']
				this.articleCategoryNum = articleSummary['articleCategoryNum']
				this.labelSumList = articleSummary['labelSumList']
				this.categorySumList = articleSummary['categorySumList']

			})

		},
		methods: {

			openDialog(id) {
				this.showDialog = true;
				if (id == 'label') {
					this.sumList = this.labelSumList
					this.dialogTitle = '标签'
					this.sumType = 'label'
				} else {
					this.sumList = this.categorySumList
					this.dialogTitle = '分类'
					this.sumType = 'category'
				}
			},
			handleCloseDialog(newVal) {
				if (newVal) {
					this.sumType = ''
					this.sumList = []

				}
				this.showDialog = newVal;
			}
		},
	};
</script>

<style scoped>
	.profile-card {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	.description {
		color: #6b779a;
		font-size: 1rem;
		margin-bottom: 1rem;
	}

	.stats {
		display: flex;
		justify-content: space-around;
		width: 100%;
		max-width: 400px;
		border-top: 1px solid #ebebeb;
		padding-top: 1rem;
	}

	.stat-item {
		text-align: center;
		font-weight: bold;
		color: #6b779a;
	}

	.el-button {
		/* 无框 透明 */
		border: none;
		background-color: transparent;
	}

	p {
		line-height: 1.0;
		/* 调整行间距 */
	}
</style>